import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { NzCascaderOption } from 'ng-zorro-antd/cascader';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { ProductService } from 'src/app/cafe/services/product.service';

@Component({
  selector: 'app-bill1',
  templateUrl: './bill1.component.html',
  styleUrls: ['./bill1.component.scss']
})
export class Bill1Component implements OnInit, OnChanges {
  // i tng ha n
  @Input() invoices: VhOrderInvoices
  // khch hng hin ti c c n hay khng?
  @Input() debt: boolean = false;
  // gi tr thu hin ti
  @Input() tax: number = 0
  // phng thc thanh ton hin ti - cu trc d liu tng thch vi component ang c s dng
  @Input() paymentType: Array<any> = [1]
  /**  kch s kin ngOnCHange cho component InvoiceDetailComponent  tnh li tin service time */
  @Input() updateServiceTimePrice = false;
  @Output() updateInvoice: EventEmitter<boolean> = new EventEmitter()
  nzOptions: NzCascaderOption[] = [
    { value: 1, label: this.languageService.translate("Cash"), isLeaf: true },
    { value: 2, label: this.languageService.translate("Debit"), disabled: true, isLeaf: true },
    { value: 3, label: this.languageService.translate("Account"), children: [] }
  ]
  /** Danh sch thu c to v ang c kch hot */
  listTax: any[] = this.vhQueryCafe.getlocalTaxs().filter(tax => tax.status);
  percent_discount = 0;
  constructor(
    private languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    public vhAuth:VhAuth,
    private productService: ProductService,
  ) { }

  ngOnInit(): void {
  }

  ngOnChanges(changes) { 
    this.getWallets(); 
    this.percent_discount = this.invoices.getDiscount() / this.invoices.getSubTotal() * 100
    if (changes['updateServiceTimePrice'] && changes['updateServiceTimePrice'].currentValue !== undefined && this.productService.isContainServiceTime(this.invoices)) {
      this.invoices.setSubTotal();
    }
  } 

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  getWallets() {
    this.nzOptions[2].children = this.vhQueryCafe.getlocalWallets()
      .filter(wallet => wallet._id != 'id_cash' &&
        wallet.id_branch == this.vhQueryCafe.getDefaultBranch()._id
      ).map(wallet => {
        return { label: wallet.name, value: wallet._id, isLeaf: true }
      })
  } 
}
