import { ConfigSalesComponent } from './components/config-sales/config-sales.component';
import { ConfigPrintComponent } from './components/config-print/config-print.component';
import { AccountInformationComponent } from './components/account-information/account-information.component';
import { KeyboardModule } from '../../keyboard/keyboard.module';
import { Bill1Component } from './components/invoice-detail/bill1/bill1.component';
import { BillDetail1Component } from './components/invoice-detail/bill-detail1/bill-detail1.component';
import { playerFactory } from 'src/app/app.module';
import { LottieModule } from 'ngx-lottie';
import { RouterModule, Routes } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from '../ng-zorro-ant.module';
import { ReactiveFormsModule,FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InvoiceDetailComponent } from './components/invoice-detail/invoice-detail.component';
import { CafeDesktopComponent } from './cafe-desktop.component';
import { PaymentInfoComponent } from './components/invoice-detail/payment-info/payment-info.component';
import { ConfigPrintKitchenComponent } from './components/config-print-kitchen/config-print-kitchen.component';
import { ConfigPrintKitchenElectronComponent } from './components/config-print-kitchen-electron/config-print-kitchen-electron.component';
const routes: Routes = [
  {
    path: '',
    component: CafeDesktopComponent,
  },
  // {
  //   path: 'cart2',
  //   loadChildren: () => import('./components/cart/cart.module').then(m => m.CartModule)
  // },
  {
    path: 'list-invoice',
    loadChildren: () => import('./components/list-invoice/list-invoice.module').then(m => m.ListInvoiceModule)
  },
  {
    path: "switch-table",
    loadChildren: () => import("./components/advange/switch-table/switch-table.module").then(m => m.SwitchTableModule)
  },
  {
    path: "seperate-table",
    loadChildren: () => import("./components/advange/seperate-table/seperate-table.module").then(m => m.SeperateTableModule)
  },
  {
    path: "booking",
    loadChildren: () => import("./components/advange/booking/booking.module").then(m => m.BookingModule)
  },
  {
    path: 'cart1',
    loadChildren: () => import('./components/cart1/cart1.module').then(m => m.Cart1Module)
  },
]


@NgModule({
  declarations: [
    CafeDesktopComponent,
    InvoiceDetailComponent,
    BillDetail1Component,
    Bill1Component,
    PaymentInfoComponent,
    AccountInformationComponent,
    ConfigPrintComponent,
    ConfigSalesComponent,
    ConfigPrintKitchenComponent,
    ConfigPrintKitchenElectronComponent
  ],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    RouterModule.forChild(routes),
    LottieModule.forRoot({ player: playerFactory }),
    KeyboardModule
  ]
})
export class CafeDesktopModule { }
