import { NzModalService } from 'ng-zorro-antd/modal';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { ChangeDetectorRef, Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { NzMessageService } from 'ng-zorro-antd/message';
import { ProductService } from 'src/app/cafe/services/product.service';
@Component({
  selector: 'app-invoice-detail',
  templateUrl: './invoice-detail.component.html',
  styleUrls: ['./invoice-detail.component.scss']
})
export class InvoiceDetailComponent implements OnInit {
  @Input() invoices: VhOrderInvoices;
  @Input() id_area: any
  @Input() id_table: any
  @Input() indexTab: any
  @Input() searchValue: any
  @Input() filterType: any
  @Input() error_to_get_bill = false
  /**  kch s kin ngOnCHange cho component InvoiceDetailComponent  tnh li tin service time */
  @Input() updateServiceTimePrice = false;
  @Output() not_exist_invoice = new EventEmitter<any>()
  /** Sau khi get bill details xong s kim tra xem c cha service time hay khng v emit ra cho component cha */
  @Output() isContainServiceTime = new EventEmitter<boolean>();
  checkPrinterKitchen: boolean = false;
  checkPrinter: boolean = false;
  // cho php n hay khng
  debt: boolean = false;
  // thng tin bn ang c t
  tableInfo: string = '';
  // trng thi
  statusInvoice: any = {
    updating: false,
    deleting: false
  }
  // thuc tnh my in
  printer: any;

  isLoadedInvoice: boolean = false

  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private router: Router,
    public vhAuth: VhAuth,
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private cdRef: ChangeDetectorRef,
    private mess: NzMessageService,
    private modal: NzModalService,
    private productService: ProductService,
  ) {

  }

  ngOnInit(): void { }

  ngOnChanges({ invoices ,error_to_get_bill}): void {
    // console.log('change');

    if (invoices) { this.initInvoice(); this.isLoadedInvoice = false; }
  }

  ngAfterViewChecked() {
    this.cdRef.detectChanges()
  }
  /**Pop up bo khi pht sinh li 
   * @param err thng tin li
  */
  alertErr(err: string) {
    this.mess.error(this.languageService.translate("Li kt ni"))
  }
  /**Khi to ha n
    * @param dataTrans: d liu state
    */
  initInvoice() {
    // console.log(this.invoices);
    if (this.invoices) {
      this.invoices = new VhOrderInvoices(this.invoices,[],this.vhQueryCafe)

      this.invoices.setIdTable(this.id_table)
      this.invoices.setIdEmployee(this.vhAuth.getUser()._id)
      this.invoices.setIdArea(this.id_area)

      // ly thu 
      let tax: any = this.vhQueryCafe.getlocalTaxs().find(tax => tax.status && tax.default);
      if (tax) this.invoices.setTax(parseFloat(tax.value))
      if (this.invoices.getID()) {
        // bn  sinh bill 
        Promise.all([
          this.vhQueryCafe.getBill(this.invoices.getID()),
          this.vhQueryCafe.getBill_details_byId_bill(this.invoices.getID()),
        ])
          .then(([bill, billDetail]) => {
            if (!bill) {
              this.modal.confirm({
                nzTitle: '<i>' + this.languageService.translate("The invoice is not existed") + '</i>',
                nzIconType: "info-circle"
              });
              this.not_exist_invoice.emit(true)
              // this.vhComponent.alertMessage("", "", this.languageService.translate("The invoice is not existed"), "OK");
              return
            }
            this.invoices = new VhOrderInvoices(bill, this.vhAlgorithm.sortDatebyDESC(billDetail,'time_start_order'),this.vhQueryCafe)
            // console.log(this.invoices);

            // Kim tra nu c Dch v thi gian trong bill detail
            if(this.productService.isContainServiceTime(this.invoices)) {
              this.isContainServiceTime.emit();
            }

            // kim tra cho php n
            this.debt = this.invoices.getIdPartner() == "id_retail" ? false :
              this.vhQueryCafe.getlocalCustomer(this.invoices.getIdPartner())
            this.isLoadedInvoice = true;
          }).catch(err => this.alertErr(err))
        // ly thng tin bn
        if (this.invoices.getIdTable() == "go_home") {
          this.tableInfo = this.languageService.translate("Take away")
        } else {
          this.tableInfo = `${this.vhQueryCafe.getlocalArea(this.invoices.getIdArea()).name} - ${this.vhQueryCafe.getlocalTable(this.invoices.getIdTable()).name}`
        }
         // kim tra my in
         let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales')
         this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : null
         if (this.printer) {
           this.checkPrinter = this.printer.enable
         }
         let printer_kitchen = this.vhQueryCafe.getLocalPrintPage('page_desktop_kitchen')
         this.checkPrinterKitchen = printer_kitchen ? printer_kitchen.print_sizes[printer_kitchen.print_sizes.findIndex(e => e._id == printer_kitchen.print_size_default)].enable : null
      }
      else { // neu bn cha co bill
        // console.log('bn cha co bill');
        this.invoices = null
        this.isLoadedInvoice = true;
      }


    }
  }


  createBill() {
    let dataRestore = { indexTab: this.indexTab, searchValue: this.searchValue, filterType: this.filterType, id_area: this.id_area }
    if (this.id_area != 'go_home' && !this.id_table) {
      this.mess.warning(this.languageService.translate("Please select a table"))
    }
    else {
      this.vhComponent.showLoading('').then(() => {
        this.vhQueryCafe.createBill_Billdetail({
          payment_type: 1,
          date: (new Date()).toISOString(),
          id_customer: 'id_retail',
          tax_buyerDisplayName: 'Ngi mua khng ly ho n',
          tax_buyerLegalName: '',
          tax_buyerTaxCode: '',
          tax_buyerAddressLine: '',
          tax_buyerEmail: '',
          id_table: this.id_table,
          id_area: this.id_area,
          id_branch: this.vhQueryCafe.getDefaultBranch()._id,
          id_employee: this.vhAuth.getUser()._id,
          id_wallet: 'id_cash',
          total: 0,
          payment: 0,
          tax: 0,
          discount: 0,
          fee: 0,
          bill_type: 5,
          note: '',
          cash: 0,
        }, [])
          .then((bill: any) => {
            if (this.id_table != 'go_home') {
              Promise.all([
                this.vhQueryCafe.updateTable(this.id_table, { status: true }),
                this.vhComponent.hideLoading(0)
              ])
                .then(() => {
                  this.router.navigate(['/cafe/cafe-desktop/cart1'],
                    {
                      state: { id_table: this.id_table, id_area: this.id_area, id_bill: bill._id, dataRestore }
                    })
                })
            }
            else
              this.vhComponent.hideLoading(0).then(() => {
                this.router.navigate(['/cafe/cafe-desktop/cart1'],
                  {
                    state: { id_table: this.id_table, id_area: this.id_area, id_bill: bill._id, dataRestore }
                  })
              })

          }, (error: any) => {
            console.log('error ', error);
          })

      })
    }
  }


}
