import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from './../../../../../components/vh-component/vh-component';
import { AfterViewInit, Component, EventEmitter, HostListener, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth, VhEinvoice } from 'ionic-vhframeworks';
import { NzCascaderOption } from 'ng-zorro-antd/cascader';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { NzMessageService } from 'ng-zorro-antd/message';
import { SalesA4Component } from 'src/app/cafe/components/print/sales/sales-a4/sales-a4.component';
import { SalesA5Component } from 'src/app/cafe/components/print/sales/sales-a5/sales-a5.component';
import { SalesK80Col4Component } from 'src/app/cafe/components/print/sales/sales-k80-col4/sales-k80-col4.component';
import { SalesK80Col3Component } from 'src/app/cafe/components/print/sales/sales-k80-col3/sales-k80-col3.component';
import { SalesK57Col3Component } from 'src/app/cafe/components/print/sales/sales-k57-col3/sales-k57-col3.component';
import { ProductService } from 'src/app/cafe/services/product.service';
import { VhType } from 'src/app/cafe/interface/vh-type';
import { BillService } from 'src/app/cafe/services/bill.service';

@Component({
  selector: 'app-payment-info',
  templateUrl: './payment-info.component.html',
  styleUrls: ['./payment-info.component.scss']
})
export class PaymentInfoComponent implements OnInit {
  // thuc tnh my in
  printer: any;
  // i tng ha n
  @Input() invoices: VhOrderInvoices
  // khch hng hin ti c c n hay khng?
  @Input() debt: boolean = false;
  // gi tr thu hin ti
  @Input() tax: number = 0
  // phng thc thanh ton hin ti - cu trc d liu tng thch vi component ang c s dng
  @Input() paymentType: Array<any> = [1];
  @Output() deleteBill = new EventEmitter<any>();
  nzOptions: NzCascaderOption[] = [
    { value: 1, label: this.languageService.translate("Cash"), isLeaf: true },
    { value: 2, label: this.languageService.translate("Debit"), disabled: true, isLeaf: true },
    { value: 3, label: this.languageService.translate("Account"), children: [] }
  ]
  /** Danh sch thu c to v ang c kch hot */
  listTax: any[] = this.vhQueryCafe.getlocalTaxs().filter(tax => tax.status);
  invoice_cash = '';
  // bien kim tra show bn phm o hay ko
  show_keyboard: boolean = this.vhAuth.localStorageGET("show_keyboard");
  checkPrinterKitchen: boolean = false;
  checkPrinter: boolean = false;
  moneyEdit: any = {}
  percent_discount = 0;
  constructor(
    private languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private modal: NzModalService,
    public vhAuth: VhAuth,
    private mess: NzMessageService,
    private nzModalService: NzModalService,
    private productService: ProductService,
    private vhEinvoice: VhEinvoice,
    private billService: BillService,
  ) { }
  // test:''
  ngOnInit(): void {
    this.initInvoice()
    this.getWallets();
    this.invoice_cash = this.invoices.getCash().toString();
    // kim tra my in
    let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales')
    this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : null
    if (this.printer) {
      this.checkPrinter = this.printer.enable
    }
    let printer_kitchen = this.vhQueryCafe.getLocalPrintPage('page_desktop_kitchen')
    this.checkPrinterKitchen = printer_kitchen ? printer_kitchen.print_sizes[printer_kitchen.print_sizes.findIndex(e => e._id == printer_kitchen.print_size_default)].enable : null
  }


  // bt s kin key press
  @HostListener('window:keydown', ['$event'])
  keyEvent(event: KeyboardEvent) {
    if (["F1", "F2", "F3",].includes(event.key)) {

      switch (event.key) {
        case "F1": document.getElementById("order-and-print").click(); break;
        case "F2": document.getElementById("order").click(); break;
        case "F3": document.getElementById("print-temp").click(); break;
      }
      event.preventDefault()
      event.stopPropagation()
    }
  }

  /**Khi to ha n
    * @param dataTrans: d liu state
    */
  initInvoice() {
    console.log(this.invoices);
    if (this.invoices) {
      this.invoices = new VhOrderInvoices(this.invoices, [], this.vhQueryCafe)
      this.invoices.setIdEmployee(this.vhAuth.getUser()._id)

      // ly thu 
      let tax: any = this.vhQueryCafe.getlocalTaxs().find(tax => tax.status && tax.default);
      if (tax) this.invoices.setTax(parseFloat(tax.value))
      if (this.invoices.getID()) {
        // bn  sinh bill
        console.log('bn  sinh bill');
        console.log(this.invoices.getID());
        Promise.all([
          this.vhQueryCafe.getBill(this.invoices.getID()),
          this.vhQueryCafe.getBill_details_byId_bill(this.invoices.getID()),
        ])
          .then(([bill, billDetail]) => {
            if (!bill) {
              this.modal.confirm({
                nzTitle: '<i>' + this.languageService.translate("The invoice is not existed") + '</i>',
                nzIconType: "info-circle"
              });
              // this.vhComponent.alertMessage("", "", this.languageService.translate("The invoice is not existed"), "OK");
              return
            }
            this.productService.updateServiceTimePriceInvoiceDetail(billDetail, this.invoices.getPriceType());
            this.invoices = new VhOrderInvoices(bill, this.vhAlgorithm.sortDatebyDESC(billDetail, 'time_start_order'), this.vhQueryCafe);
            this.invoices.setSubTotal();

            // kim tra cho php n
            this.debt = this.invoices.getIdPartner() == "id_retail" ? false :
              this.vhQueryCafe.getlocalCustomer(this.invoices.getIdPartner())
            this.percent_discount = this.invoices.getDiscount() / this.invoices.getSubTotal() * 100
          }).catch(err => this.alertErr(err))
      }
      else { // neu bn cha co bill
        console.log('bn cha co bill');
        this.invoices = null
      }


    }
  }

  /**Pop up bo khi pht sinh li 
   * @param err thng tin li
  */
  alertErr(err: string) {
    this.mess.error(this.languageService.translate("Li kt ni"))
  }

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    setTimeout(() => {
      this.moneyEdit['cash'] = this.vhAlgorithm.vhnumeral(".customerPay1")
    }, 500);
  }

  // ngDoCheck(): void {
  //   if(document.getElementsByClassName('customerPay1')) this.moneyEdit['cash'] = this.vhAlgorithm.vhnumeral(".customerPay1")
  // }


  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  getWallets() {
    this.nzOptions[2].children = this.vhQueryCafe.getlocalWallets()
      .filter(wallet => wallet._id != 'id_cash' &&
        wallet.id_branch == this.vhQueryCafe.getDefaultBranch()._id
      ).map(wallet => {
        return { label: wallet.name, value: wallet._id, isLeaf: true }
      })
  }

  /**
     * kim tra ti a 12 ky t cu cash
     * @param value 
     */
  checkMaxLengthCash(value: string) {
    if (parseFloat(value) > 999999999999) { value = value.slice(0, 12); this.invoice_cash = value }
  }

  updateCash() {
    this.invoices.setCash(parseFloat(this.moneyEdit['cash'].getRawValue()));
    this.vhQueryCafe.updateBill(this.invoices.getID(), { cash: parseFloat(this.moneyEdit['cash'].getRawValue()) })
      .then((bool) => {
        // console.log('updateBill', bool);
      }, error => {
        this.vhComponent.alertMessageDesktop('error', this.languageService.translate("An error while updating bill"))
      })
  }

  /**Save ha n */
  createProcess() {
    // Nu c cha dch v thi gian th cp nhp cho ccc bill detail na
    if(this.productService.isContainServiceTime(this.invoices)) {
      this.productService.updateAllServiceTimeToPause(this.invoices.getInvoiceDetail());
      if (this.invoices.getIdTable() == 'go_home') {
        return Promise.all([
          this.vhQueryCafe.updateBill_Billdetail(
            this.invoices.getID(),
            this.invoices.getCreateUpdateInvoice(1),
            this.invoices.getCreateUpdateInvoiceDetail()
          ),
        ]);
      } 
      else {
        return Promise.all([
          this.vhQueryCafe.updateBill_Billdetail(
            this.invoices.getID(),
            this.invoices.getCreateUpdateInvoice(1),
            this.invoices.getCreateUpdateInvoiceDetail()
          ),
          this.vhQueryCafe.updateTable(this.invoices.getIdTable(), {
            status: false,
          }),
        ]);
      }
    }
    else {
      if (this.invoices.getIdTable() == 'go_home') {
        return Promise.all([this.vhQueryCafe.saveBill_Billdetail(this.invoices.getID(), 1)])
      } 
      else {
        return Promise.all([
          this.vhQueryCafe.saveBill_Billdetail(this.invoices.getID(), 1),
          this.vhQueryCafe.updateTable(this.invoices.getIdTable(), { status: false })
        ])
      }
    }
  }
  /** Thanh ton ha n
   * @param print: cho php in ha n hay k?
  */
  saveInvoice(print: 'print' | 'notPrint') {
    if (print == "print") {
      if (this.checkPrinter) {
        this.vhComponent.showLoading('').then(() => {
          const bill: any = {...this.invoices};
          const invoiceTemp = {...bill?.invoice};
          this.createProcess()
            .then(async (res) => {
              if(this.cqt_submit_invoice_immediately) await this.handleMinvoice(invoiceTemp);
              this.vhComponent.hideLoading(0);
              this.mess.success(`${this.languageService.translate("Sales invoice")} ${this.invoices.getBillCode()} ${this.languageService.translate("has been created successfully")}`);
              if (print == "print" && this.checkPrinter) this.handlePrintInvoice();
              this.deleteBill.emit(true)
            }).catch(err => {
              this.vhComponent.hideLoading(0);
              this.mess.error("Li kt ni")
            })
        })

      }
      else {
        this.nzModalService.confirm({
          nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
          nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
          nzIconType: "info-circle",
          nzCancelText: null,
          nzOnOk: () => { }
        })
      }
    }
    else {
      this.vhComponent.showLoading('').then(() => {
        const bill: any = {...this.invoices};
        const invoiceTemp = {...bill?.invoice};
        this.createProcess()
          .then(async (res) => {
            if(this.cqt_submit_invoice_immediately) await this.handleMinvoice(invoiceTemp);
            this.vhComponent.hideLoading(0);
            this.mess.success(`${this.languageService.translate("Sales invoice")} ${this.invoices.getBillCode()} ${this.languageService.translate("has been created successfully")}`);
            this.deleteBill.emit(true)
          }).catch(err => {
            this.vhComponent.hideLoading(0);
            this.mess.error("Li kt ni")
          })
      })
    }
  }

  /**Render food name 
  * @param id_prod: id product
  * @param id_subProd: id subproduct
 */
  renderNameFood(id_prod: string, id_subProd?: string) {
    let food = this.vhQueryCafe.getlocalProduct(id_prod)
    let subFood: any;

    if (id_subProd)
      subFood = food.subs.find(item => item._id == id_subProd)
    return `${food ? food.name : ''} ${subFood ? '(' + subFood.name + ')' : ''}`
  }
  /**Render cc thnh phn combo 
  * @package arr: mng combos
 */
  renderSubCombo(arr: any[]) {
    let subName: string = ""
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`
      else subName += `${item.quantity} ${item.name}`
    })
    return subName
  }

  /** M trang in */
  handlePrintInvoice() {
    this.vhQueryCafe.getBill(this.invoices.getID()).then((res: any) => {
      this.invoices.setBillCode(res.bill_code);
      this.vhComponent.showModal(this.renderPrintPage(this.printer['_id']), { printer: this.printer, invoice: this.invoices }, false, false, `modal-print-${this.printer['_id']}`).then((modal) => {
        modal.onWillDismiss().then(() => { })
      })
    });
   
  }

  /** M trang Print out for customer */
  handlePrintTest() {
    if (this.checkPrinter) {
      if (this.vhAuth.checkMyPermission('sales_enable_print_out_for_customer'))
        this.vhComponent.showModal(this.renderPrintPage(this.printer['_id']), { printer: this.printer, invoice: this.invoices, temp: true }, false, false, `modal-print-${this.printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => { })
        })
      else
        this.mess.error(this.languageService.translate("You do not have this rights"), { nzDuration: 5000 })
    }
    else this.nzModalService.confirm({
      nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
      nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
      nzIconType: "info-circle",
      nzCancelText: null,
      nzOnOk: () => { }
    })
  }

  ///////////////////////////////////////////////IN HA N//////////////////////////////////////////////////////////
  /**
   * Ly cc trang in
   * @param type size
   * @returns 
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return SalesK57Col3Component
      case 'print_size_k80_3c': return SalesK80Col3Component
      case 'print_size_k80_4c': return SalesK80Col4Component
      case 'print_size_a5': return SalesA5Component
      case 'print_size_a4': return SalesA4Component
      default: return SalesA4Component
    }
  }



  /* ----------------------------------------------------------- HA N IN T ---------------------------------------------------------- */

  cqt_invoice_auto_sign: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').cqt_invoice_auto_sign
  cqt_submit_invoice_immediately: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').cqt_submit_invoice_immediately

  handleMinvoice(bill) {
    return new Promise((resolve, reject) => {
      // ly ra chi nhnh hin ti
      let currentBranch = this.vhQueryCafe.getlocalBranch(this.vhQueryCafe.getDefaultBranch()._id);

      if (!currentBranch.einvoices) {
        resolve(true)
        return
      }
      // trng hp cha c to kt ni ha n in t
      if (!currentBranch.einvoices?.length) {
        this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Please connect e-invoice for this branch"))
        resolve(true)
        return
      }
      // trng hp  gi ln cqt ri th khi
      if(bill.cqt_status == 2 || bill.cqt_status == 3){
        resolve(true)
        return
      }
      // trng hp ch c 1 kt ni ha n in t th to thng lun
      if (currentBranch.einvoices.length && currentBranch.einvoices.length == 1) {
        if (!currentBranch.einvoices[0].invoiceSeries.length) {
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Please connect e-invoice for this branch"))
          resolve(true)
          return
        }
        this.billService.changeBill_byLocal_toInvoice_byMinvoice(bill._id, currentBranch.einvoices[0].invoiceSeries[0])
          .then((rsp) => {
            console.log('changeBill_toInvoice_byMinvoice', rsp)
            if (rsp.vcode == 0) {
              if (this.cqt_invoice_auto_sign) {
                this.vhEinvoice.saveInvoice_Sign_byMinvoice(currentBranch.taxcode, currentBranch.einvoices[0].token, 1, rsp.data)
                  .then((res: any) => {
                    console.log('saveInvoice_Sign_byMinvoice', res);
                    if (res.code == '00') {
                      bill.cqt_status = 3
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 3 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      }, error => {
                        console.log('error', error);
                      })
                    }
                    else if (res.code == '9999') {
                      bill.cqt_status = 1
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng kim tra li thng tin ha n y ln CQT cha ph hp"))
                    }
                    else {
                      bill.cqt_status = 1
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                     
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate('An error occurred while sending the invoice to CQT'))
                    }
                  })
              }
              else {
                this.vhEinvoice.saveInvoice_noSign_byMinvoice(currentBranch.taxcode, currentBranch.einvoices[0].token, 1, rsp.data)
                  .then((res: any) => {
                    console.log(res);
                    if (res.code == '00') {
                      bill.cqt_status = 2
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 2 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      }, error => {
                        console.log('error', error);
                      })
                    }
                    else if (res.code == '9999') {
                      
                      bill.cqt_status = 1
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng kim tra li thng tin ha n y ln CQT cha ph hp"))
                    }
                    else {
                      
                      bill.cqt_status = 1
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate('An error occurred while sending the invoice to CQT'))
                    }
                  })
              }
            }
          }, (error: any) => {
            console.log('error', error)
            resolve(true)
            return
          })
      }
    })
  }

}
