  /** Danh sch khu vc */
  public area: any[] = this.vhQueryCafe.getlocalAreas().filter(item => item.status && item.id_branch == this.vhQueryCafe.getDefaultBranch()._id);
  /** ID khu vc hot ng */
  public areaID: any = 'go_home';
  public tableID: any = 'go_home';
  id_table_selected: any // bin luu tm id table  chn
  listTables: any = [];
  indexTab: number = 0;
  /** d liu state truyn v */
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  /** Danh sch ty chn b lc */
  listFilter: Array<any> = [
    { value: 1, name: "Latest" },
    { value: 2, name: "Oldest" },
    { value: 3, name: "Low to High value" },
    { value: 4, name: "High to Low value" }
  ]
  /**Subscription area - table*/
  area$: Subscription
  observableTables: Subscription

  invoiceSelected: any;
  checkPrinter: boolean = false;
  checkPrinterKitchen: boolean = false;
  /** thuc tnh my in */
  printer: any;
  openBillStatus = false; // bien check thanh ton
  isloaded = false; // bien check tt icon  loading
  debt = false;
  kitchens = this.vhQueryCafe.getlocalDispatcherZones()
  showConfigPrintKitchenElectron = false
  showModalSelectOrderInterface = false;
  showConfig = false //  bin show modal cu hnh
  showConfigPrint = false//  bin show modal cu hnh may in
  showConfigPrintKitchen = false//  bin show modal cu hnh may in bp
  showConfigSales = false//  bin show modal cu hnh trang bn hng
  modalAccountInfo: boolean = false;//show/hide moal thng tin ti khon
  user: any = this.vhAuth.getUser() // thng tin ti khon hin ti
  templates: { name: string; url: string; giai_thich: string; }[] = [];
  store_main_sector = '';
  country_code = 'VN';
  language = 'vi';
  error_to_get_bill: boolean;

  constructor(
    public vhQueryCafe: VhQueryCafe,
    private router: Router,
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private cdRef: ChangeDetectorRef,
    private vhQuery: VhQuery,
    public vhAlgorithm: VhAlgorithm,
    private functionService: FunctionService,
    private translateService: TranslateService,
    private mess: NzMessageService,
    private platform:Platform,
    private nzModalService: NzModalService,
    private notification: NzNotificationService,
    private http: HttpClient,
    public vhAuth: VhAuth) {
    if (this.router.getCurrentNavigation().extras.state) {
      this.areaID = this.router.getCurrentNavigation().extras.state.id_area;

    }
  }

  ngOnInit() {
    console.log(this.vhQueryCafe.getlocalCustomers());

    this.startTime.setHours(0, 0, 0, 0);
    this.endTime.setHours(23, 59, 59, 59);
    /** Ly d liu area */
    this.getAreaData();
    let splash = document.getElementById("lottie-sync")
    if (splash) splash.remove();
    this.checkExpireAccount()
    this.checkBranches()
    if (this.dataRestore) {
      /* Nhn d liu t router truyn sang */
      this.searchValue = this.dataRestore.searchValue || ''
      this.filterType = this.dataRestore.filterType || 1
      this.indexTab = this.dataRestore.indexTab
      this.filterTitle = this.renderSort(this.filterType)

      if (this.dataRestore.id_area) {
        this.areaID = this.dataRestore.id_area
      } else this.areaID = this.area[0]._id
    }
    // x l nu khu vc mang v b disable
    /** Ly d liu bill mang v */
    if (this.vhQueryCafe.getlocalArea('go_home').status) this.getBillDataTakeAway();
    else {
      this.listTables = this.functionService.sortNameIncludeNumber(this.vhQueryCafe.getlocalTables().filter(item => item.id_area == this.areaID)).map(item => { return { ...item, selected: false } })
      if (this.listTables.length) this.getBillByTable(this.listTables[0])
    }
    this.user = this.vhAuth.getlocalEmployee(this.vhAuth.getUser()._id)
    // kim tra my in
    let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_sales')
    this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : null
    if (this.printer) {
      this.checkPrinter = this.printer.enable
    }
    let printer_kitchen = this.vhQueryCafe.getLocalPrintPage('page_desktop_kitchen')
    this.checkPrinterKitchen = printer_kitchen ? printer_kitchen.print_sizes[printer_kitchen.print_sizes.findIndex(e => e._id == printer_kitchen.print_size_default)].enable : null
    this.store_main_sector = this.vhAuth.getStore().main_sector; // Ly main_sector ca user
    this.http.get(`assets/documents/configuration/template/${this.country_code}/${this.store_main_sector}.json`).subscribe((templates) => {
      this.templates = templates[this.language];
    }); // Ly giao din theo main_sector ca ca hng
  }

  /**
  * kim tra ti khon ht hn cha
  */
